<script>
	export default {
		name: 'manageContent',
		data() {
			return {
				drafts: [],
			}
		},
		methods: {
			deleteItem(id) {
				this.$_db.collection('draft_post').doc(id).delete()
			},
		},
		firestore () {
		   return {
		     drafts: this.$_db.collection('draft_post')
		   }
		},
	}
</script>
